
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.weather {
  @media screen and (min-width: 320px) {
    width: 288px;
    height: 469px;
    padding: 32px 36px 84px 36px;
 }
 @media screen and (min-width: 768px) {
  width: 353px;
  height: 675px;
  padding: 40px 32px 130px 32px;
}
@media screen and (min-width: 1280px) {
  width: 395px;
  padding: 40px 53px 130px 53px;
}

margin-left: auto;
margin-right: auto;
background: #4440F7;
mix-blend-mode: normal;
border-radius: 4px;
}

.weather-header {
  @media screen and (min-width: 320px) {
    margin-bottom: 40px;
 }
 @media screen and (min-width: 768px) {
  margin-bottom: 90px;
}
  display: flex;
}

.weather-temp {
  @media screen and (min-width: 320px) {
    font-size: 55px;
    line-height: 1.16;
 }
 @media screen and (min-width: 768px) {
  font-size: 64px;
line-height: 1.17;
}
@media screen and (min-width: 1280px) {
  font-size: 64px;
line-height: 1.17;
margin-right: 8px;
}

font-family: 'Roboto', sans-serif;
font-style: normal;
font-weight: 400;
color: #FFFFFF;
}

.weather-main {
  border-left: 1px solid #FFFFFF;
  padding-left: 8px;
}

.weather-status {
  @media screen and (min-width: 320px) {
    font-size: 24px;
    line-height: 1.16;
 }
 @media screen and (min-width: 768px) {
  font-size: 36px;
line-height: 1.17;
}
@media screen and (min-width: 1280px) {
  font-size: 36px;
line-height: 1.17;
}

font-family: 'Roboto', sans-serif;
font-style: normal;
font-weight: 400;
color: #FFFFFF
}

.weather-city_box {
  @media screen and (min-width: 320px) {
    width: 121px;
    height: 34px;
 }
 @media screen and (min-width: 768px) {
  width: 177px;
  height: 42px;
  
}
display: flex;
justify-items: center;

padding: 8px;

background: rgba(254, 254, 255, 0.3);
border-radius: 10px;
border: none;
}

.weather-city {
  @media screen and (min-width: 320px) {
    font-size: 14px;
    line-height: 1.14;
 }
 @media screen and (min-width: 768px) {
  font-size: 20px;
  line-height: 1.15;
}

  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 400;
  color: #FFFFFF;
  }



.location-icon {
  @media screen and (min-width: 320px) {
    width: 18px;
    height: 18px;
 }
 @media screen and (min-width: 768px) {
  width: 27px;
  height: 27px;
  
}
  fill: #FFFFFF;
   margin-right: 8px;
}



.weather-icon {
  @media screen and (min-width: 320px) {
    width: 128px;
    height: 121px;
    margin-bottom: 40px;
 }
 @media screen and (min-width: 768px) {
  width: 165px;
  height: 156px;
  margin-bottom: 90px;
}

  display: block;
  margin-left: auto;
  margin-right: auto;

mix-blend-mode: normal;
}

.weather-date {
  @media screen and (min-width: 320px) {
    font-size: 28px;
    line-height: 1.18;
 }
 @media screen and (min-width: 768px) {
  font-size: 36px;
line-height: 1.17;
}

font-family: 'Roboto', sans-serif;
font-style: normal;
font-weight: 400;
text-align: center;
color: #FFFFFF;
}